@keyframes snow {
  0% {
    background-position: 0 0, 0 0, 0 0;
  }

  100% {
    background-position: -500px 1000px, -400px 400px, -300px 300px;
  }
}

body:before {
  content: "";
  opacity: .5;
  background: url("https://i.imgur.com/YFhfZNz.png"), url("https://i.imgur.com/BPySOkm.png"), url("https://i.imgur.com/2ARguPW.png") center fixed;
  width: 100%;
  height: 100%;
  animation: 20s steps(600, end) infinite snow;
  position: absolute;
}
